import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./SignIn/SignIn.css";
import "./pages.css";
import NavBar from "./navbar/NavBar";
import logo from './logo.jpg';
import planet from './planet.png';
import rocket from './rocket.jpg';
import space from './space.jpg';


class Home extends Component {
  constructor(props) {
    super();
    this.state = {};
  }

changeRoute = (newRoute) => {
  this.props.changeRoute(newRoute);
}
loadUser = (data) =>{
  this.props.loadUser(data);
}

  render() {
    return (
      <div>
        <NavBar changeRoute={this.changeRoute} />

        <div className="tc">
          <br />
          <br />
          <br />
          <h1 className="tc white i f1 font">The AstroPhysics Calculator</h1>
          <br />
          <br />
          <Container>
            <Row>
           
              <Col className="tc f3 solidBR">
                  So what do we do?
               <hr />
                <div className="tl f3 i">
                  We have creates a simple yet powerful program to simplify 
                  anstrophysics. After stuggling through those PSETS, we 
                  decided there must be a better way. Now there is ... 
                  <br/>               
                </div>
              </Col>

                <Col className=''>
            
             <img src={logo} alt="wo" />
               </Col>
          

                      </Row>

              <br/>
              <br/>
              <br/>
              <br/>
              <br/>

              <Row>

              <Col className=''>
            
             <img src={planet} alt="yes" />
               </Col>

           
              <Col className="tc f3 solidBR">
                  Struggling with Orbitals?

                <hr />
                <div className="tl f3 i">
                  We got you. We can find:
                  <br/>
                  - Velocity <br/>
                  - Gravitanal constant <br/>
                  - Mass of Large body <br/>
                  - Gravitanal constant <br/>
                  - Radius <br/>
                  <br/>               
                </div>
              </Col>

                
          

                      </Row>

                      <br/>
                      <br/>
              <br/>
              <br/>
              <br/>
                      <Row>

           
               
           
              <Col className="tc f3 solidBR">
                  Struggling with Rocket equation?

                <hr />
                <div className="tl f3 i">
                  I am too. We can find:
                  <br/>
                  - Change in Velocity <br/>
                  - Effective Exhaust Velocity <br/>
                  - Total Initial Mass <br/>
                  - Total Final Mass <br/>
                  <br/>               
                </div>
              </Col>

                   <Col className=''>
            
             <img src={rocket} alt="w" />
               </Col>
          

                      </Row>

              <br/>

              <br/>

              <br/>
              <br/>
              <br/>

            <Row>

              
               <Col className=''>
            
             <img src={space} alt='w'/>
               </Col>
           
              <Col className="tc f3 solidBR">
                  Just curiuos about space?

                <hr />
                <div className="tl f3 i">
                  Who isn't? We can find:
                  <br/>
                  - Your age on other planets <br/>
                  - What you call people from each planet <br/>
                  - Your weight on other planets <br/>
                  - How high you can jump on other planets <br/>
                  
                  <br/>               
                </div>
              </Col>

              

                
          

        </Row>
          </Container>
          <br/>
          <br/>
        </div>
        </div>
    );
  }
}

export default Home;
